// 用来数据共享数据存储
const state = {
    loginDialogVisible: false, // 是否显示登录模块
    isLogin: false, // 是否登录
    userInfo: {} // 用户信息
}

// 用来对共享数据进行过滤操作
const getters = {
    watchIsLogin: (state) => {
        const userInfo = JSON.parse(window.localStorage.getItem("userInfo"));
        if (userInfo && userInfo.UserToken) 
            state.isLogin = true
        else 
            state.isLogin = false
        
        return state.isLogin
    }
};

// 用来注册改变数据状态
const mutations = {
    // 显示隐藏登录界面
    SWITCH_USER_LOGIN_DIALOG_VISIBLE: (state, dialogVisible) => {
        state.loginDialogVisible = dialogVisible;
    },
    // 设置用户信息
    SET_USER_INFO: (state, userInfo) => {
        state.userInfo = userInfo;
    },
    // 设置用户是否登录
    SWITCH_IS_LOGIN: (state, isLogin) => {
        state.isLogin = isLogin;
    }
};

// 解决异步改变共享数据
const actions = {
    // 改变登录界面是否显示
    switchUserLoginDialogVisible({ commit }, loginDialogVisible) {
        commit("SWITCH_USER_LOGIN_DIALOG_VISIBLE", loginDialogVisible);
    },
    // 改变用户登录后的信息
    setUserInfo({ commit }, userInfo) {
        window.localStorage.setItem("userInfo", JSON.stringify(userInfo));
        commit("SET_USER_INFO", userInfo);
        commit("SWITCH_IS_LOGIN", true);
        commit("SWITCH_USER_LOGIN_DIALOG_VISIBLE", false);
    },
    // 退出
    logout({ commit }) {
        window.localStorage.removeItem("userInfo");
        window.localStorage.removeItem("DeviceId");
        commit("SET_USER_INFO", {});
        commit("SWITCH_IS_LOGIN", false);
    }
};

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters
};


