<template>
    <div class="m-swiper-box">
        <swiper :options="swiperOption" class="m-swiper-item">
            <swiper-slide
                class="m-swiper-slide-item"
                v-for="(item,index) in swiperData"
                :key="index"
            >
                <slot name="slide" />
            </swiper-slide>
            <!-- 如果需要分页器 -->
            <div class="swiper-pagination"  slot="pagination" v-if="pagination"></div>
            <!-- 如果需要导航按钮 -->
            <div class="swiper-button-prev" slot="button-prev" v-if="navigation"></div>
            <div class="swiper-button-next" slot="button-next" v-if="navigation"></div>
            <!-- 如果需要滚动条 -->
            <div class="swiper-scrollbar" slot="scrollbar" v-if="scrollbar"></div>
        </swiper>
    </div>
</template>

<script>
// import vueSwiper from 'vue-awesome-swiper'
// import 'swiper/dist/css/swiper.css'
export default {
    name: 'mswiper',
    props: {
        pagination: {type: Boolean, default: false}, // 是否显示分页器
        navigation: {type: Boolean, default: false}, // 是否显示前进后退按钮
        scrollbar:  {type: Boolean, default: false}, // 是否需要滚动条

        swiperData: {type:Array, default: [1,2,3,4,5]}, // swiper数据
        swiperOption: {type: Object, default: this.swiperOptions}, // swiper配置
    },
    data() {
        return {
            swiperOptions: {
                speed: 500, // 切换时间
                autoplay: { // 自动播放
                    delay: 3000, // 自动切换停留时长
                    disableOnInteraction: false, // 触碰后是否停止
                    stopOnLastSlide: false, // 如果设置为true，当切换到最后一个slide时停止自动切换。（loop模式下无效）
                    reverseDirection: false, // 开启反向自动轮播。
                    waitForTransition: false, // 等待过渡完毕。自动切换会在slide过渡完毕后才开始计时
                },
                loop: false, // 是否循环播放
                lazy: { // 延迟加载
                    loadPrevNext: true,
                },
                grabCursor: true, // 鼠标覆盖Swiper时指针会变成手掌形状
                centeredSlides: false, // 居中
                // slidesPerView: 'auto',
                slidesPerView: 1,
                slidesPerGroup: 1, // 定义slides的数量多少为一组
                slidesPerColumn: 1, // 显示2行
                slidesPerColumnFill: 'row', //‘column’ 多行布局中以什么形式填充
                effect: 'coverflow', // 切换效果 还有 “fade，cube，flip”
                coverflowEffect: {
                    rotate: 20,
                    stretch: -10,
                    depth: 50,
                    modifier: 1,
                    slideShadows : true
                },
                // 如果需要分页器
                pagination: {
                    el: ".swiper-pagination",
                    clickable: true
                },
                spaceBetween: 15, // slide之间设置距离（单位px）
                
                // 如果需要滚动条
                scrollbar: {
                    el: '.swiper-scrollbar',
                },
                // 如果需要前进后退按钮
                navigation: {
                    nextEl: '.swiper-button-next',
                    prevEl: '.swiper-button-prev',
                },
            },
        }
    },
    created() {

    },
    methods: {

    }
}
</script>

<style lang="less">
.m-swiper-box{
    width:100%;
    height:100%;
    position:relative;
    top:0;
    left:0;
}
.m-swiper-item{
    display: block;
    width:100%;
    height:100%;
    & > .swiper-pagination-bullets {
        bottom: 5px;
        left: 0;
        width: 100%;
        .swiper-pagination-bullet {
            width: 10px;
            height: 10px;
            box-shadow: 0px 0px 1px 2px #fff inset;
        }
        .swiper-pagination-bullet-active{
            background: #fff;
            box-shadow: 0px 0px 1px 3px rgba(0, 0, 0, 0.6) inset;
        }
    }
}
.m-swiper-slide-item{
    display: block;
    width:100%;
    height:100%;
    background-color:#ccc;
    display: flex;
    justify-content: center;
    align-items: center;
}
</style>


