import Vue from 'vue';

import mContainer from './m_container';
import mCenter from './m_center';
import mHeader from './m_header';
import mFooter from './m_footer';
import mScroll from "./m_scroll";

import mDialog from './dialog/dialog.vue';

import mActionSheet from "./actionsheet/actionsheet.vue"
import mProgress from "./progress/progress.vue"
import mUploader from "./uploader/uploader.vue"
import mPicker from "./picker/picker.vue"
import mDataPicker from "./datapicker/datapicker.vue";
import mRadio from "./radio/radio.vue";
import mRadioGroup from "./radio/radiogroup.vue";
import mCheckbox from "./checkbox/checkbox.vue";
import mCheckboxGroup from "./checkbox/checkboxgroup.vue";
import mSwiper from "./swiper/swiper.vue";
import mSwipe from "./mswipe/swipe.vue";
import mSwipeItem from "./mswipe/swipe-item.vue";


import mScrollNav from "./mscroll/mscrollnav/scroll-nav.vue";
import mScrollNavPanel from "./mscroll/mscrollnav/scroll-nav-panel.vue";

Vue.component('m-container', mContainer);
Vue.component('m-center', mCenter);
Vue.component('m-header', mHeader);
Vue.component('m-footer', mFooter);
Vue.component("m-scroll", mScroll);

Vue.component("m-swipe", mSwipe);
Vue.component("m-swipe-item", mSwipeItem);
Vue.component("m-scroll-nav", mScrollNav);
Vue.component("m-scroll-nav-panel", mScrollNavPanel);

Vue.component("m-dialog", mDialog);

Vue.component("m-actionsheet", mActionSheet);
Vue.component("m-progress", mProgress);
Vue.component("m-uploader", mUploader);
Vue.component("m-picker", mPicker);
Vue.component("m-datepicker", mDataPicker);
Vue.component("m-radio", mRadio);
Vue.component("m-radiogroup", mRadioGroup);
Vue.component("m-checkbox", mCheckbox);
Vue.component("m-checkboxgroup", mCheckboxGroup);
Vue.component("m-swiper", mSwiper);


