<template>
    <div class="cube-loading">
        <span class="cube-loading-spinners" :style="style">
            <i class="cube-loading-spinner" v-for="(item,i) in balde" :key="i"></i>
        </span>
    </div>
</template>
<script type="text/ecmascript-6">
const COMPONENT_NAME = 'cube-loading'

export default {
  name: COMPONENT_NAME,
  data() {
    return {
      balde: 12
    }
  },
  props: {
    size: {
      type: Number
    }
  },
  computed: {
    style() {
      if (!this.size) {
        return
      }
      const value = `${this.size}px`
      return {
        width: value,
        height: value
      }
    }
  }
}
</script>
<style lang="less" rel="stylesheet/less">

.cube-loading{
    font-size: 24px;
}
  
.cube-loading-spinners{
    position: relative;
    display: block;
    width: .4rem;
    height: .4rem;
}

.loading(@count) when (@count > 0) {
  .loading((@count - 1));
  &:nth-child(@{count}) {
    animation-delay: ((@count) / 12s);
    transform: rotate(30deg * (@count - 6)) translateY(-150%);
  }
  
}
  
.cube-loading-spinner{
    position: absolute;
    left: 45%;
    top: 35%;
    width: 2px;
    height: 25%;
    border-radius: 50%/20%;
    opacity: .25;
    background-color: currentColor;
    animation: spinner-fade 1s linear infinite;
    .loading(12)
}
  
@keyframes spinner-fade{
    0%{
        opacity: .85;
    }  
    50%{
        opacity: .25;
    }  
    100%{
        opacity: .25;
    }  
}
  
</style>



