<template>
    <div class="login-pop" v-if="loginDialogVisible">
        <div class="login-page">
            <div class="header flex flex-between">
                <div class="back flex-shrink-0" @click="closeLogin"><i class="icon-close"></i></div>
            </div>
            <div class="n-wrap p-t-60 p-l-40 p-r-40">
                <div class="content p-t-60 p-b-60">
                    <img class="block logo" src="https://m.meb.com/Content/images/login/logo@3x.png" alt="logo">
                </div>
                <div class="login-form p-t-30 p-l-30 p-r-30">
                    <!--手机号码-->
                    <div class="form-item border-bottom flex">
                        <input type="number" class="font-30 c-33 input-item" 
                            v-on:input="hasVal"
                            id="user" v-model="mobile" autocomplete="off"
                            maxlength="11" placeholder="请输入您的手机号" />
                    </div>
                    <!--验证码-->
                    <div class="form-item border-bottom flex flex-between m-t-20">
                        <input type="number" class="font-30 c-33 input-item code-left"
                            v-on:input="hasVal" 
                            autocomplete="off" id="code"
                            maxlength="6"  v-model="code"
                            placeholder="请输入验证码" />
                        <button class="input-item font-30 c-33 flex-shrink-0 code-right" v-if="!smsStatus" @click="onSendSms"><span class="font-24 w-full c-33">获取验证码</span></button>
                        <button class="input-item font-30 c-33 flex-shrink-0 code-right" v-if="smsStatus" disabled>
                            <count-down :value="30" @finish="onFinish"></count-down>
                        </button>
                        <!-- <button type="button" class="input-item font-30 c-33 flex-shrink-0 code-right" :disabled="disabled" @click="getCode">{{getCodeText}}</button> -->
                    </div>
                    <!--登录-->
                    <button class="m-t-60 login_btn" :class="{'disabled' : disabled}" @click="login"></button>
                </div>
            </div>
            <!--协议-->
            <div class="agreement c-1F201F font-22 one-line">
                登录即同意<a class="c-meb" href="//m2.meb.com/statement">《用户服务协议》</a>和<a class="c-meb" href="//m2.meb.com/hideinfo">《隐私保护政策》</a>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState,mapGetters } from 'vuex'
import sendCode from '@/mixin/sendCode'
import CountDown from '@/components/countdown'
export default {
    name:"login",
    mixins: [sendCode],
    components: {CountDown},
    props: {},
    data() {
        return {
            mobile:'',
            code:'',
            getCodeText: '发送验证码',
            smsStatus:false,
            disabled:true, // 禁用按钮
        }
    },
    computed: {
        ...mapState({
            loginDialogVisible: state => state.user.loginDialogVisible,
            isLogin: state => state.user.isLogin
        }),
        ...mapGetters({
            watchIsLogin: 'user/watchIsLogin'
        })
    },
    watch: {
        mobile(curVal, oldVal) {
            if(!/^[0-9]+$/.test(curVal) || curVal.length > 11) {
                this.mobile =  curVal.slice(0, curVal.length-1)
            }
        },
        code(curVal, oldVal) {
            if(!/^[0-9]+$/.test(curVal) || curVal.length > 6) {
                this.code =  curVal.slice(0, curVal.length-1)
            }
        }
    },
    beforeCreate() {
        const userInfo = JSON.parse(window.localStorage.getItem('userInfo'));
        if(userInfo && userInfo.UserToken){
            this.$store.dispatch('user/switchUserLoginDialogVisible', false)
            this.$store.dispatch('user/setUserInfo', userInfo)
        }
    },
    created() {
        this.watchBodyIsScroll(this.loginDialogVisible)
    },
    methods:{
        closeLogin() {
            if(this.watchIsLogin){
                this.$store.dispatch('user/switchUserLoginDialogVisible', false)
                this.watchBodyIsScroll(this.loginDialogVisible)
            }else{
                setTimeout(() => {
                    this.$store.dispatch('user/switchUserLoginDialogVisible', false)
                    this.watchBodyIsScroll(this.loginDialogVisible)
                },100)
                this.$router.go(-1)
            }
        },
        watchBodyIsScroll(islogin) {
            if(islogin) {
                document.body.style.overflow = 'hidden';
            } else {
                document.body.style.overflow = 'auto';
            }
        },
        hasVal() {
            if(this.mobile && this.code) this.disabled = false
            else this.disabled = true
        },
        onSendSms() {
            if (!/^1[345789]\d{9}$/.test(this.mobile)) {
                //提示错误消息
                this.$tips.text("电话号码错误，请检查后重新输入");
                return;
            }
            this.smsStatus = true;
        },
        onFinish() {
            this.smsStatus = false;
        },
        login() {
            if (!/^1[345789]\d{9}$/.test(this.mobile)) {
                //提示错误消息
                this.$toast.top("手机号码错误，清检查后重新输入");
                return;
            }else if(!this.code){
                this.$toast.top("请输入验证码");
                return;
            }
            var model = {
                mobile: this.mobile,
                vcode: this.code
            }
            this.$http.user.goLogin(model)
                .then(res => {
                    if(res.data.Success){
                        this.$emit('loginSuccessCallback')
                        this.$toast.top("登录成功");
                        setTimeout(() => {
                            this.$store.dispatch('user/setUserInfo', res.data.Content)
                            this.watchBodyIsScroll(this.loginDialogVisible)
                        },100)
                        
                    }else{
                        this.$toast.top(res.data.Message);
                    }
                })
                .catch((err)=>{
                    this.$toast.top(err)
                })
        }
    }
}
</script>

<style lang="less" scoped>
.icon-close {
    display: block;
    width: 0.36rem;
    height: 0.36rem;
    background: url(https://m.meb.com/Content/images/login/exit@3x.png) no-repeat center;
    background-size: contain;
    margin-right:0.1rem;
}
.logo{
    display:block;
    margin:auto;
    width:1.4rem;
    height:1.4rem;
}

.header {
    width:100%;
    height:.88rem;
    padding:0 .3rem;
}

.login-form{
    .login_btn{
        width: 100%;
        height: .88rem;
        overflow: hidden;
        border-radius: 5rem;
        background:url(https://m.meb.com/Content/images/login/btn_login_act@3x.png)no-repeat center;
        background-size:contain;
        &.disabled{
            background:url(https://m.meb.com/Content/images/login/btn_login@3x.png)no-repeat center !important;
            background-size:contain !important;
        }
    }
    .form-item{
        width: 100%;
        height: 1.08rem;
        line-height: 1.08rem;
        overflow: hidden;
        .input-item{
            width:100%;
            height:.68rem;
            line-height: .68rem;
            text-decoration: none;
            outline: none;
            -webkit-tap-highlight-color: transparent;
            &.code-left{
                width:50%;
            }
            &.code-right{
                width:auto;
                height:.68rem;
                line-height: .68rem;
                background-color:transparent;
            }
        }
    }
}

.border-bottom {
    border-bottom:1px solid #f0f0f0;
}
.login-pop {
    position:fixed;
    top:0;
    bottom:0;
    max-width: 414px;
    width:100%;
    z-index:10000000;
    background:url(https://m.meb.com/Content/images/login/login-bg.png) no-repeat center;
    background-size:cover;
    .login-page{
        position:relative;
        top:0;left:0;
        width:100%;
        height:100%;
        .agreement{
            margin-top:1.2rem;
            width:100%;
            padding:.3rem;
            text-align: center;
        }
    }
}
</style>
<style lang="css">
input[type=number] {
    -moz-appearance: textfield;
    box-shadow: none;
    -webkit-appearance: none;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus {
    background-color: #fff !important;
    background-image: none !important;
    color: #292C33 !important;
    box-shadow: 0 0 0 4rem #fff inset !important;
    -webkit-text-fill-color: #292C33 !important;
}
</style>


