'use strict';

//import Index from '../views/Index/index';

const Index = q => require.ensure([], () => q(require('../views/Home/index')), 'bundle');
const Test = q => require.ensure([], () => q(require('../views/Home/test')), 'bundle');

const Search = q => require.ensure([], () => q(require('../views/Search/index')), 'bundle');

const Error = q => require.ensure([], () => q(require('../views/Error/index')), 'bundle');

const Example = q => require.ensure([], () => q(require('../views/Example/index')), 'bundle');
const ScrollDefault = q => require.ensure([], () => q(require('../views/Example/scroll/default')), 'bundle');
const ScrollNavs = q => require.ensure([], () => q(require('../views/Example/scroll/scroll-navs')), 'bundle');
const Picker = q => require.ensure([], () => q(require('../views/Example/picker')), 'bundle');
const Corpper = q => require.ensure([], () => q(require('../views/Example/corpper')), 'bundle');
const Echarts = q => require.ensure([], () => q(require('../views/Example/echart')), 'bundle');

/**
 * Consult 免费咨询
 */
const Consult = q => require.ensure([], () => q(require('../views/Consult/index')), 'bundle');

/*
* Project 项目
*/
const Project = q => require.ensure([], () => q(require("../views/Project/index")), "bundle");
const ProjectDetail = q => require.ensure([], () => q(require("../views/Project/detail")), "bundle");
const ProjectZDetail = q => require.ensure([], () => q(require('../views/Project/zdetail')), 'bundle');

/*
* News 整形攻略
*/
const News = q => require.ensure([], () => q(require("../views/News/index")), "bundle");
const NewsList = q => require.ensure([], () => q(require("../views/News/list")), "bundle");
const NewsDetail = q => require.ensure([], () => q(require("../views/News/detail")), "bundle");

/**
 * Zs 整形知识
 */
const Zs = q => require.ensure([], () => q(require("../views/Zs/index")), "bundle");
const ZsDetail = q => require.ensure([], () => q(require("../views/Zs/detail")), "bundle");

/**
 * Question 整形问答
 */
const Question = q => require.ensure([], () => q(require("../views/Question/index")), "bundle");
const QuestionList = q => require.ensure([], () => q(require("../views/Question/list")), "bundle");
const QuestionDetail = q => require.ensure([], () => q(require("../views/Question/detail")), "bundle");

/**
 * Diary 整形日记
 */
const Diary = q => require.ensure([], () => q(require("../views/Diary/index")), "bundle");
const DiaryList = q => require.ensure([], () => q(require("../views/Diary/list")), "bundle");

/*
* Hospital 医院
*/
const HospitalList = q => require.ensure([], () => q(require("../views/Hospital/list")), "bundle");
const HospitalDetail = q => require.ensure([], () => q(require("../views/Hospital/detail")), "bundle");

/**
 * Doctor 医生
 */
const DoctorList = q => require.ensure([], () => q(require("../views/Doctor/list")), "bundle");
const DoctorDetail = q => require.ensure([], () => q(require("../views/Doctor/detail")), "bundle");

/**
 * Video 视频
 */
const Video = q => require.ensure([], () => q(require("../views/Video/index")), "bundle");
const VideoList = q => require.ensure([], () => q(require("../views/Video/list")), "bundle");
const VideoDetail = q => require.ensure([], () => q(require("../views/Video/detail")), "bundle");

/**
 * User 个人中心
 */
const User = q => require.ensure([], () => q(require("../views/User/index")), "bundle");
const UserSetting = q => require.ensure([], () => q(require("../views/User/setting")), "bundle");

/**
 * Order 订单
 */
const Order = q => require.ensure([], () => q(require("../views/Order/index")), "bundle");
const AllOrder = q => require.ensure([], () => q(require("../views/Order/all")), "bundle");
const WaitPay = q => require.ensure([], () => q(require("../views/Order/waitpay")), "bundle");
const WaitCharge = q => require.ensure([], () => q(require("../views/Order/waitcharge")), "bundle");
const AlreadyConsumed = q => require.ensure([], () => q(require("../views/Order/alreadyconsumed")), "bundle");
const AfterSales = q => require.ensure([], () => q(require("../views/Order/aftersales")), "bundle");

const routes = [
    {
        path: "*",
        component: Error
    },
    {
        name: "home",
        path: "/home",
        component: Index,
        meta: {
            keepAlive: true,
            title: "首页"
        }
    },
    {
        name: "test",
        path: "/test",
        component: Test,
        meta: {
            keepAlive: false,
            title: "测试"
        }
    },
    {
        name: "example",
        path: "/example",
        component: Example,
        meta: {
            keepAlive: false,
            title: "测试"
        }
    },
    {
        name: "echart",
        path: "/echart",
        component: Echarts,
        meta: {
            keepAlive: false,
            title: "echart图表"
        }
    },
    {
        name: "scroll.default",
        path: "/example/scroll",
        component: ScrollDefault,
        meta: {
            keepAlive: false,
            title: "scroll"
        }
    },
    {
        name: "scroll.scrollnav",
        path: "/example/scrollnavs",
        component: ScrollNavs,
        meta: {
            keepAlive: false,
            title: "scroll-nav"
        }
    },
    {
        name: "picker",
        path: "/picker",
        component: Picker,
        meta: {
            keepAlive: false,
            title: "picker"
        }
    },
    {
        name: "corpper",
        path: "/corpper",
        component: Corpper,
        meta: {
            keepAlive: false,
            title: "corpper"
        }
    },
    { path: "/", redirect: "/home" },
    {
        name: "search",
        path: "/search",
        component: Search,
        meta: {
            keepAlive: false,
            title: "搜索"
        }
    },
    // 免费咨询
    {
        name: "consult",
        path: "/consult",
        component: Consult,
        meta: {
            keepAlive: false,
            title: "美呗提问"
        }
    },
    //项目百科
    {
        name: "project.index",
        path: "/project",
        component: Project,
        meta: {
            keepAlive: true,
            title: "项目"
        }
    },
    {
        name: "project.detail",
        path: `/project/:id(\\d+)`,
        component: ProjectDetail,
        meta: {
            keepAlive: false,
            title: "项目百科详情"
        }
    },
    {
        name: "project.zdetail",
        path: `/project/:id([z][0-9]+)`,
        component: ProjectZDetail,
        meta: {
            keepAlive: false,
            title: "项目详情"
        }
    },
    //整形攻略
    {
        name: "news.index",
        path: "/news",
        component: News,
        meta: {
            keepAlive: true,
            title: "整形攻略"
        }
    },
    {
        name: "news.list",
        path: "/news/:spellname([a-z]+)/:page(\\d+)?/:parm(\\d+)?",
        component: NewsList,
        meta: {
            keepAlive: false,
            title: "整形攻略分类"
        }
    },
    {
        name: "news.detail",
        path: "/news/:id(\\d+).html",
        component: NewsDetail,
        meta: {
            keepAlive: false,
            title: "整形攻略详情"
        }
    },
    //整形知识
    {
        name: "zs.index",
        path: "/zs",
        component: Zs,
        meta: {
            keepAlive: true,
            title: "整形知识"
        }
    },
    {
        name: "zs.detail",
        path: "/zs/:id(\\d+).html",
        component: ZsDetail,
        meta: {
            keepAlive: false,
            title: "整形知识详情"
        }
    },
    //整形问答
    {
        name: "question.index",
        path: "/question",
        component: Question,
        meta: {
            keepAlive: true,
            title: "整形问答"
        }
    },
    {
        name: "question.list",
        path: "/question/:spellname([a-z]+)/:page(\\d+)?/:parm(\\d+)?",
        component: QuestionList,
        meta: {
            keepAlive: false,
            title: "整形问答分类"
        }
    },
    {
        name: "question.detail",
        path: "/question/:id(\\d+).html",
        component: QuestionDetail,
        meta: {
            keepAlive: false,
            title: "整形问答详情"
        }
    },
    //整形日记 Diary
    {
        name: "diary.index",
        path: "/diary",
        component: Diary,
        meta: {
            keepAlive: true,
            title: "整形日记"
        }
    },
    {
        name: "diary.list",
        path: "/diary/:spellname([a-z]+)/:page(\\d+)?/:parm(\\d+)?",
        component: DiaryList,
        meta: {
            keepAlive: false,
            title: "整形日记"
        }
    },
    //医院
    {
        name: "hospital.list",
        path: "/hospital/:spellname([a-z]+)/:areaname?/:page?/:sort?/:type?",
        component: HospitalList,
        meta: {
            keepAlive: false,
            title: "整形医院"
        }
    },
    {
        name: "hospital.detail",
        path: "/hospital/:id(\\d+).html",
        component: HospitalDetail,
        meta: {
            keepAlive: false,
            title: "整形医院详情"
        }
    },
    //医生
    {
        name: "doctor.list",
        path: "/doctor/:spellname([a-z]+)/:areaname?/:page?/:sort?/:type?",
        component: DoctorList,
        meta: {
            keepAlive: false,
            title: "整形医生"
        }
    },
    {
        name: "doctor.detail",
        path: "/doctor/:id(\\d+).html",
        component: DoctorDetail,
        meta: {
            keepAlive: false,
            title: "整形医生详情"
        }
    },
    //视频
    {
        name: "video.index",
        path: "/video",
        component: Video,
        meta: {
            keepAlive: false,
            title: "视频"
        }
    },
    {
        name: "video.list",
        path: "/video/:spellname([a-z]+)/:page(\\d+)?/:parm(\\d+)?",
        component: VideoList,
        meta: {
            keepAlive: false,
            title: "视频分类"
        }
    },
    {
        name: "video.detail",
        path: "/video/:id(\\d+).html",
        component: VideoDetail,
        meta: {
            keepAlive: false,
            title: "视频详情"
        }
    },
    //个人中心
    {
        name: "user.index",
        path: "/user",
        component: User,
        meta: {
            keepAlive: true,
            title: "个人中心",
            auth: true
        }
    },
    {
        name: "user.setting",
        path: "/user/setting",
        component: UserSetting,
        meta: {
            keepAlive: false,
            title: "设置",
            auth: true
        }
    },
    {
        // name: "order.index",
        path: "/order",
        component: Order,
        meta: {
            keepAlive: false,
            title: "我的订单",
            auth: true
        },
        children: [
            { path: "", redirect: "/order/all" },
            {
                name: "order.all",
                path: "/order/all",
                component: AllOrder,
                meta: {
                    keepAlive: true
                }
            },
            {
                name: "order.waitpay",
                path: "/order/waitpay",
                component: WaitPay,
                meta: {
                    keepAlive: true
                }
            },
            {
                name: "order.waitcharge",
                path: "/order/waitcharge",
                component: WaitCharge,
                meta: {
                    keepAlive: true
                }
            },
            {
                name: "order.alreadyconsumed",
                path: "/order/alreadyconsumed",
                component: AlreadyConsumed,
                meta: {
                    keepAlive: true
                }
            },
            {
                name: "order.aftersales",
                path: "/order/aftersales",
                component: AfterSales,
                meta: {
                    keepAlive: true
                }
            }
        ]
    }
];
export default routes;


