<template>
    <div class="m-scroll">
        <scroll
            ref="scroll"
            :scroll-events="scrollEvents"
            :data="data"
            :options="options"
            @scroll="scroll"
            @pulling-down="onPullingDown"
            @pulling-up="onPullingUp">
            <slot></slot>
            <template v-if="customPullDown" slot="pulldown" slot-scope="props">
                <div
                    v-if="props.pullDownRefresh"
                    class="cube-pulldown-wrapper"
                    :style="props.pullDownStyle">
                    <div
                        v-show="props.beforePullDown"
                        class="before-trigger"
                        :style="{paddingTop: props.bubbleY + 'px'}">
                        <span :class="{'rotate': props.bubbleY > pullDownRefreshThreshold - 40}">↓</span>
                    </div>
                    <div class="after-trigger" v-show="!props.beforePullDown">
                    <div v-show="props.isPullingDown" class="loading">
                        <loading></loading>
                    </div>
                    <div v-show="!props.isPullingDown" class="text"><span class="refresh-text">Refresh success</span></div>
                    </div>
                </div>
            </template>
        </scroll>
    </div>
</template>

<script>
import scroll from "@/components/mscroll/scroll";
import Loading from './mscroll/loading.vue';

export default {
    components:{ scroll, Loading },
    props: {
        data: {type: [Array, String, Object], default() { return [] }},
        showScrollbar: {type: Boolean, default: true},
        fadeScrollbar: {type: Boolean, default: true},
        customPullDown: {type: Boolean, default: false},
        pullDown: {type: Object, default() {
            return {
                pullDownRefresh: false,
                pullDownRefreshThreshold: 60,
                pullDownRefreshStop: 40,
                pullDownRefreshTxt: '刷新成功',
            }
        }},
        pullUp: {type: Object, default() {
            return {
                pullUpLoad: false,
                pullUpLoadThreshold: 0,
                pullUpLoadMoreTxt: '加载更多',
                pullUpLoadNoMoreTxt: '没有更多数据',
            }
        }}
    },
    data() {
        return {
            scrollEvents: ['scroll'],
            scrollbar: this.showScrollbar,
            scrollbarFade: this.fadeScrollbar,
            startY: 0,
            scrollToY: 0,
            scrollToTime: 700,

            pullDownRefresh: this.pullDown.pullDownRefresh || false,
            pullDownRefreshThreshold: this.pullDown.pullDownRefreshThreshold || 60,
            pullDownRefreshStop: this.pullDown.pullDownRefreshStop || 40,
            pullDownRefreshTxt: this.pullDown.pullDownRefreshTxt || '刷新成功',

            pullUpLoad: this.pullUp.pullUpLoad || false,
            pullUpLoadThreshold: this.pullUp.pullUpLoadThreshold || 0,
            pullUpLoadMoreTxt: this.pullUp.pullUpLoadMoreTxt || '加载更多',
            pullUpLoadNoMoreTxt: this.pullUp.pullUpLoadNoMoreTxt || '没有更多数据',
        }
    },
    computed: {
        options() {
            return {
                scrollbar: this.scrollbarObj,
                startY: this.startY,

                pullDownRefresh: this.pullDownRefreshObj,
                pullUpLoad: this.pullUpLoadObj,
            }
        },
        scrollbarObj() {
            return this.scrollbar ? { fade: this.scrollbarFade } : false
        },
        pullDownRefreshObj: function() {
            return this.pullDownRefresh ? {
                threshold: parseInt(this.pullDownRefreshThreshold),
                // Do not need to set stop value, but you can if you want
                // stop: parseInt(this.pullDownRefreshStop),
                txt: this.pullDownRefreshTxt
            } : false
        },
        pullUpLoadObj: function() {
            return this.pullUpLoad ? {
                threshold: parseInt(this.pullUpLoadThreshold),
                txt: {
                    more: this.pullUpLoadMoreTxt,
                    noMore: this.pullUpLoadNoMoreTxt
                }
            } : false
        }
    },
    watch: {
        options: {
            handler() {
                this.rebuildScroll()
            },
            deep: true
        },
    },
    mounted(){

    },
    methods:{
        scroll(pos) {
            // console.log(pos)
        },
        scrollTo() {
            console.log('执行scrollTo事件');
            this.$refs.scroll.scrollTo(
                0,
                this.scrollToY,
                this.scrollToTime
            )
        },
        onPullingDown() {
            this.$emit('PullingDown')
            // 模拟更新数据
            // setTimeout(() => {
            //     if (Math.random() > 0.5) {
            //         // 如果有新数据
            //             console.log(111111)
            //         // this.items.unshift(_foods[1])
            //         setTimeout(()=>{
            //             console.log('5秒后停止刷新')
            //             this.$refs.scroll.forceUpdate()
            //         },5000)
            //     } else {
            //         // 如果没有新数据
            //         console.log(22222)
            //         this.$refs.scroll.forceUpdate()
            //     }
            // }, 1000)
        },
        onPullingUp() {
            this.$emit('PullingUp')
            // 更新数据
            // setTimeout(() => {
            //     if (Math.random() > 0.5) {
            //         // 如果有新数据
            //         // let newPage = _foods.slice(0, 5)
            //         // this.items = this.items.concat(newPage)
            //         console.log('1111111111')
            //         setTimeout(()=>{
            //             console.log('5秒后停止刷新')
            //             this.$refs.scroll.forceUpdate()
            //         },5000)
            //     } else {
            //     // 如果没有新数据
            //     console.log(22222222222)
            //         this.$refs.scroll.forceUpdate()
            //     }
            // }, 1000)
        },
        rebuildScroll() {
            this.$nextTick(() => {
                this.$refs.scroll.destroy()
                this.$refs.scroll.initScroll()
            })
        }
    }
}
</script>
<style lang="less">
// .scroll-wrapper {
    .cube-pulldown-wrapper {
        .before-trigger {
            font-size: 30px;
            line-height: 30px;
            align-self: flex-end;
            span {
                display: inline-block;
                font-size: .2rem;
                transition: all 0.3s;
                color: #666;
                &.rotate {
                    font-size: .3rem;
                    transform: rotate(180deg);
                }
            }
            .after-trigger{
                .refresh-text{
                    color: grey;
                }
            }
        }
    }
// }
  
    
      
      
        
</style>
<style lang="less" scoped>

</style>


