"use strict";
import http from "../http";
import options from "../http/config";

const user = {
    /**
     * 获取验证码
     * @param {mobile}
     */
    getSmsCode(mobile) {
        // return http.post("/app/v5/Wap/RequestValidCode", mobile);
        return http.get(`/js/v7/user/RequestValidCode?mobile=${mobile}`);
    },

    /**
     * 登录
     * @param {*}
     */
    goLogin(model) {
        return http.post(`/js/v7/user/userlogin`, model);
    },

    /**
     * 获取我的最新订单
     * @param {*}
     */
    getOrderDate() {
        return http.get(`/app/v8/user/UserCenterData?isWap=true`);
    }
};

export default user;



