export default {
    data() {
        return {

        }
    },
    methods: {
        // 发送验证码
        getCode() {
            var _time = parseInt(new Date().getTime() / 1000); // 时间戳1
            _time = _time + 15;
            var _newt, _endT;
            if (this.mobile) {
                var pitch = /^1[345789]\d{9}$/;
                if (!pitch.test(this.mobile)) {
                    this.$toast.top("请输入正确的手机号");
                    return false;
                } else {
                    this.$http.user.getSmsCode(this.mobile)
                        .then(res => {
                            if (res.data.Success) {
                                this.$toast.top("验证码发送成功，请注意查收");
                                var InterValObj = setInterval(() => {
                                    _newt = parseInt(new Date().getTime() / 1000); // 时间戳2
                                    _endT = _time - _newt;
                                    if (_endT > 0) {
                                        this.getCodeText = _endT + "s后重发";
                                        this.disabled = true;
                                    } else {
                                        this.getCodeText = "重新发送";
                                        this.disabled = false;
                                        window.clearInterval(InterValObj);
                                    }
                                }, 1000);
                            }
                        })
                        .catch(() => {
                            this.$toast.top("发送失败，请稍后再试");
                        });
                }
            } else {
                this.$toast.top("手机号码不能为空");
                this.disabled = false;
                return false;
            }
        }
    }
}


