'use strict';
import axios from 'axios';
import options from './config';
import utils from '../utils';



let instance = axios.create(
    Object.assign(
        {
            baseURL: options.baseURL,
            timeout: 10000
        },
        options
    )
);

let deviceid = utils.uuid.getId();

// 添加请求拦截器
instance.interceptors.request.use(function (config) {
    // 在发送请求之前做些什么
    let defaults = {
        "content-type": "application/json",
        DeviceVersion: "v1.0.0",
        MebDeviceId: deviceid,
        MebVersion: "v1.0.0"
    };

    let userInfo = window.localStorage.getItem("userInfo");

    if(userInfo) {
        userInfo = JSON.parse(userInfo);
        const token = userInfo.UserToken;
        if (token) {
            config.headers["UserToken"] = token;
        }
    }

    config.headers = Object.assign(config.headers, defaults);

    return config;
    
  }, function (error) {
    
    return Promise.reject(error);

  });

// 添加响应拦截器
instance.interceptors.response.use(function (response) {
    return response;
  }, function (error) {
    //请求超时处理
    if (error.message.includes("timeout")) {
        return Promise.reject(error); // reject这个错误信息
    }
    if (error.response) {
        switch (error.response.status) {
            case 401:
                // router.replace({ name: 'signIn' })
                break;
            case 404:
                console.error("接口不存在");
                break;
            case 403:
                console.error("没有访问权限");
                break;
            case 500:
                console.error("服务器错误：" + error.response.data.msg);
                break;
            default:
                console.error("出错了");
                break;
        }
    }

    return Promise.reject(error.response && error.response.data);

  });

export default instance;


