"use strict";
import http from "../http";
import options from "../http/config";

const video = {
    /**
     * 获取整形视频首页数据
     * @param {*}
     */
    getData(filter) {
        return http({
            baseURL: options.CMS_API,
            url: "/app/articleapi/video/GetMVideoIndex",
            method: "get"
        });
    },

    /**
     * 获取详情数据
     * @param {stateShowId}
     */
    getVideoDetail(filter) {
        return http({
            baseURL: options.CMS_API,
            url: "/app/articleapi/video/GetMVideoDetail",
            method: "get",
            params: filter
        });
    },

    /**
     * 获取列表页筛选数据
     * @params {sname=spellname,page=页码}
     */
    getFilter(filter) {
        return http({
            baseURL: options.YG_API,
            url: "/api/app/v1.0/Project/Select",
            method: "get",
            params: filter
        });
    },
    /**
     * 获取列表页数据
     * @params {sname=spellname,page=页码}
     */
    getVideoList(filter) {
        return http({
            baseURL: options.CMS_API,
            url: "/app/articleapi/video/GetMVideoListV2",
            method: "get",
            params: filter
        });
    }
};

export default video;



